import Vue from 'vue'
import VueRouter from 'vue-router'

import Install from '@src/components/install-doc.vue'

const routes = [
    { path: '/', name: 'install', component: Install },
    { path: '/directives', name: 'directives', component: Install },
    { path: '/functional', name: 'install', component: Install },
    { path: '/dialog', name: 'install', component: Install },
    { path: '/components', name: 'install', children: [
            { path: 'badge', name: 'badge', component: Install },
        ] 
    }
]

export default new VueRouter({
    routes
})


